<template>
    <div class="radio" :class="{active:radioValue==label}" :radioIndex="label" ref="a1" @click="handleRadio($event)">
        <span class="radio-icon"></span>
        <span class="radio-txt"><slot></slot></span>
    </div>
</template>
<script>
    export default {
        name: 'radio',
        props:{
            label:{
                type:[String,Number],
                default:null
            },
            mod:{
                type:[String,Number],
                default:null
            },
            modelName:{
                type:[String],
                default:null
            }
        },
        computed:{

        },
        watch:{
            mod(val){
                this.radioValue = val;
            }
        },
        data() {
            return {
                radioValue:0,
            }
        },
        mounted(){
            this.radioValue = this.mod;
            console.log('this.label');
            console.log(this.label);
        },
        methods:{
            //单选项点击
            handleRadio(e){
                //console.log(e);

                let refVal = this.$refs.a1.getAttribute('radioindex');
                //console.log(refVal);
                this.radioValue = refVal;

                //console.log(this.radioValue);
                //this.$emit('handleRadioEmit',this.label);
                //this.$store.commit();
            }
        }
    }
</script>
<style scoped>
    .radio{
        height: 22px;overflow: hidden;line-height: 22px;float:left;
    }
    .radio-icon{width: 16px;height: 16px;float:left;display: block;
        background: url("../../assets/images/check1.jpg") no-repeat center;
        background-size: 100%;margin-top:3px;
    }
    .radio-txt{display: block;float:left;font-size: 14px;color:#333;margin-left:5px;margin-right: 10px;}
    .radio.active .radio-icon{background: url("../../assets/images/check1_active.png") no-repeat center;
        background-size: 100%;}
</style>


