<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">Radio 单选框</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                在一组备选项中进行单选
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">由于选项默认可见，不宜过多。</div>
            <div class="hb-a">
                <Radio :mod="radio1" :label="1">单选</Radio>
                <Radio :mod="radio1" :label="2">单选2</Radio>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >

                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>

        </div>
    </div>
</template>

<script>
    import Radio from '@/components/template/Radio.vue'
    export default {
        name: 'wlradio',
        data() {
            return {
                radio1:'2',
                radio2:'2',
            }
        },
        mounted(){
            this.doCode();
            setTimeout(()=>{
                this.radio1 = '1';
            },2000)
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            Radio
        }
    }
</script>
<style scoped>

</style>



